
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import axios from 'axios';

@Injectable({
  providedIn: 'root'
})


export class EmailsService {

  constructor(private http: HttpClient) { }


  getProyects(): Observable<any> {
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
    return this.http.get<any>(`https://myprojects23.herokuapp.com/`, { headers });
  }

 async getCursos(value: any):Promise<Observable<any>> {

    const {data} = await axios.get('https://api.ipify.org/?format=json')
    value.ip = data;
    let params =value;
  
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
   // return this.http.post<any[]>(`http://localhost:5000/?phone=${params.phone}`, params, { headers });
    return this.http.post<any[]>(`https://jsendmails.herokuapp.com/?phone=${params.phone}`, params, { headers });
  }


  async getIP(): Promise<Observable<any>>{
    const {data} = await axios.get('https://api.ipify.org/?format=json');
    let params = { 
      ip: data
    }
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
   // return this.http.post<any[]>(`http://localhost:5000/ipVisitor`, params, { headers });
    return this.http.post<any[]>(`https://jsendmails.herokuapp.com/ipVisitor`, params, { headers });
  }

   getCountriesVisit(): Observable<any>{
   
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
 //return this.http.get<any[]>(`http://localhost:5000/getVisits`,  { headers });
     return this.http.get<any[]>(`https://jsendmails.herokuapp.com/getVisits`,  { headers });
  }

   getAllVisits(): Observable<any[]>{
   
    let headers = new HttpHeaders().set('Content-Type', 'application/json')
   //return this.http.get<any[]>(`http://localhost:5000/countryVisit`, { headers });
     return this.http.get<any[]>(`https://jsendmails.herokuapp.com/countryVisit/KKLFDJLJDJFL0303`,  { headers });
  }





}
