import { Component, OnInit,HostListener, ElementRef } from '@angular/core';

import {scroll,checkScroll} from '../utils/animation';



import 'firebase/firestore'
import { EmailsService } from '../utils/emails.service';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css'],
  animations: [scroll]
})
export class PortfolioComponent implements OnInit {
  items: any |null=null;

  public state = 'scrolled'


  constructor(
  
    public el: ElementRef,
    private _emailsService: EmailsService
    ) { 
      
      
      
 }

  ngOnInit(): void {
    
     this._emailsService.getProyects().subscribe(result =>{
      this.items = result.projects 
      });
  
  }

  @HostListener('window:scroll', ['$event'])
   listener ()  {
      this.state = checkScroll(this.el);
    }

}
