import { HttpClient } from '@angular/common/http';
import { Component, HostListener, OnInit, ViewChild, } from '@angular/core';

import { LocalThemeService } from './local-theme.service';




@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css',
  ]

})
export class NavbarComponent implements OnInit {

Theme: string |null=null; 
  constructor(private localThemeService: LocalThemeService) {

  }

  ngOnInit(): void {
  this.localThemeService.verifyTheme();
  this.Theme = this.localThemeService.getTheme();
    if(this.Theme=='dark'){
     
      this.setTheme();
    }
  }


   scrolltoTop(){
    document.querySelector('#Home')?.scrollIntoView({ behavior: 'smooth', block: 'center' });
    console.log("asdads")
   }
  scrollServices() {

 

    document.getElementById('Contact')?.scrollIntoView({ behavior: 'smooth',block: 'center' });


  }
  scrollAbout() {

    document.querySelector('#About')?.scrollIntoView({ behavior: 'smooth', block: 'center' });


  }

  scrollPortfolio() {

    document.querySelector('#Portfolio')?.scrollIntoView({ behavior: 'smooth', block: 'center' });


  }

  changeTheme(){

   this.localThemeService.setTheme();
   this.setTheme();
   
  }

  setTheme(){
    document?.querySelector('.nav-bar')?.classList.toggle('dark')
    document?.querySelector('body')?.classList.toggle('dark')
    document?.querySelector('.menu')?.classList.toggle('darked')
    document?.querySelector('.content-about')?.classList.toggle('dark')
    document?.querySelector('.content-portfolio')?.classList.toggle('dark')
    document?.querySelector('.content-contact')?.classList.toggle('dark')
    document?.querySelector('.content-technologies')?.classList.toggle('dark')
    document?.querySelector('.content-footer')?.classList.toggle('darkFotter')
    document?.querySelector('.theme')?.classList.toggle('fa-sun');
   
  }

  

   

  }






