import { Component, ElementRef, HostListener, OnInit } from '@angular/core';
import { scroll, checkScroll,checkStart } from '../utils/animation';
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css'],
  animations: [scroll]
})
export class AboutComponent implements OnInit {
 public state = 'scrolled'

  constructor(public el: ElementRef) { }

  ngOnInit(): void {
  this.checkStart();
  }

  @HostListener('window:scroll',['$event'])
  listener(){
    this.state = checkScroll(this.el);
  }


  checkStart(){
    setTimeout(() => {
        this.state =  checkStart(this.state);
    },400)
  }

 
}
