import { Component, ElementRef, HostListener, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { EmailsService } from '../utils/emails.service';

import { scroll, checkScroll } from '../utils/animation';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css'],
  animations: [scroll]
})
export class ContactComponent implements OnInit {

  public state = 'scrolled'
  public salida = "";
  public estado: any = "";
  public show: string = "";
  public msg: string = "";
  public email: any = "";
  public first: string = "";
  public end: string = "";
  public description: string = "";
  public phone: any = "";
  constructor(
    private _emailsService: EmailsService,
    public el: ElementRef
  ) { }

  ngOnInit(): void {
  }

  onSubmit(f: NgForm) {
   
    let body = {
      email: this.email,
      name: `${this.first} ${this.end}`,
      description: this.description,
      phone: this.phone,

    }

    if (this.email == "" && this.first == "" && this.end == "" && this.phone == "" && this.description == "") {
      this.estado = "error";
      this.msg = "complete all fields"
      this.show = "show"
      setTimeout(() => {

        this.changeEstado();
      }, 5000)
      return;
    }



  
    this.sendEmail(body).then(res=>{
      res.subscribe(() => {

      this.estado = "succes";
      this.msg = "Mail was sent, check your folder spam"
      this.show = "show"
      setTimeout(() => {
        this.changeEstado();
      }, 5000)
      f.reset();
    }, err => {
     
      this.estado = "error";
      this.msg = err.error.message;
      this.show = "show";
      setTimeout(() => {
        this.changeEstado();
      }, 5000)

    })


  })
}


  async sendEmail(body: any){
   return await this._emailsService.getCursos(body);
  }

  changeEstado() {
    if (this.estado == 'error' || this.estado == 'succes') {
      this.estado = '';
      this.show = 'hide'
    }
  }

  @HostListener('window:scroll', ['$event'])
  listener() {
  
    this.state = checkScroll(this.el);

  }

}
