import { ElementRef } from '@angular/core';
import {
    trigger,
    state,
    style,
    animate,
    transition
} from '@angular/animations';




export const scroll = [
    trigger('scrollAnimation', [
        state('scrolled', style({
            left: '50px',
           opacity: '0',
            
        })),
        state('normal', style({
            right: '0px',
           opacity: '1',
            
        })),
        transition('scrolled => normal', animate('1s ease')),
        transition('normal => scrolled', animate('1s ease'))
    ])
];


export const checkScroll= (el: ElementRef)  => {

    const componentPosition = el.nativeElement.offsetTop
    const componentPositionB = componentPosition+ el.nativeElement.clientHeight;
    const scrollPosition = window.pageYOffset

   
    if ( scrollPosition < componentPosition -400 || scrollPosition > componentPositionB -50  ) {
      console.log("Scrolled")
        return 'scrolled'
    }else{
        console.log("normal")
        return 'normal'
    }

}

