export const checkFinal = (el: ElementRef) => {

    const componentPosition = el.nativeElement.offsetTop
    
    const scrollPosition = window.pageYOffset

    if ( scrollPosition < componentPosition -400  ) {
        
        return 'scrolled'
    } else {
      
        return 'normal'
    }

}


