import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class LocalThemeService {

  constructor() { }


  getTheme()  {
    let theme = localStorage.getItem('theme');
    return theme;

  }

  setTheme() {
    let theme = localStorage.getItem('theme');
   
      if (theme === 'light') {
        localStorage.setItem('theme', 'dark');
        
      } else if (theme === "dark") {
        localStorage.setItem('theme', 'light');
      }
    
  }

  verifyTheme():boolean{
    let theme = localStorage.getItem('theme');
    if (theme) {
     return true;
    }else{
      localStorage.setItem('theme', 'light');
      return false;
    }
  }
}
